// Import core components
import { Image, OverlayTrigger, Popover, Row } from 'react-bootstrap'

// Import our components
import { useNamespace, usePublic, useStudio } from 'hooks'
import { Select } from 'components/studio'
import allMaps from '../maps.json'

// Import style
// ...

/**
 * Studio: Command & Conquer - Maps
 *
 * @returns {React.FunctionComponentElement} React.FunctionComponentElement
 */
function Maps() {
  // Hooks
  const path = useNamespace({ type: 'variables', name: 'map' })
  const publik = usePublic()
  // Redux
  const currentMap = useStudio(path)
  // Variables
  const { maps } = allMaps

  return (
    <Row className="d-flex flex-column justify-content-center align-items-center gx-2">
      <Select label="Current Map" name="map">
        {maps.map((map) => (
          <option key={map} value={map}>
            {map}
          </option>
        ))}
      </Select>
      <OverlayTrigger
        placement="top"
        overlay={
          <Popover>
            <Popover.Body>
              <Image className="mb-2" src={`${publik}/maps/preview/${currentMap}.webp`} fluid />
            </Popover.Body>
          </Popover>
        }
      >
        <a className="text-success text-center m-0">Preview</a>
      </OverlayTrigger>
    </Row>
  )
}

// Exported Component for use
export default Maps
